import { changeVisibility } from "../api/api";
export default {
  name: "setDialog",
  props: {
    setDialog: {
      default: false
    },
    setId: {
      default: false
    },
    Visi: {
      default: 3
    },
    isTeam: {
      default: false
    },
    setName: {
      default: []
    }
  },
  watch: {
    setDialog: function setDialog(val) {
      this.isV = val;
    },
    Visi: function Visi(val) {
      this.vi = val;
    }
  },
  data: function data() {
    return {
      isV: false,
      vi: -1
    };
  },
  methods: {
    cancelForm: function cancelForm() {
      this.$emit('changeVisible', false, this.setId);
    },
    submitForm: function submitForm() {
      var _this = this;

      console.log(this.vi);
      console.log(this.Visi);
      changeVisibility(this.setId, this.vi).then(function (res) {
        if (res.status === 200) {
          _this.$message({
            message: '修改权限成功！',
            type: 'info'
          });

          _this.$emit('changeVisible', false, _this.setId);
        }
      }).catch(function (e) {
        if (e.response.status === 401) _this.$message({
          message: e.response.data,
          type: 'error'
        });

        _this.$emit('changeVisible', false, _this.setId);
      });
    }
  }
};