export default {
  name: "shareDialog",
  props: {
    shareDialog: {
      default: false
    },
    shareId: {
      default: false
    },
    shareName: {
      default: ''
    }
  },
  watch: {
    shareDialog: function shareDialog(val) {
      this.isV = val;
    },
    shareId: function shareId() {
      this.init();
    }
  },
  data: function data() {
    return {
      isV: false,
      shareImg: 'https://cube.elemecdn.com/3/7c/3ea6beec64369c2642b92c6726f1epng.png'
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      this.axios.get('/qrcode/', {
        params: {
          doc: this.shareId
        },
        responseType: 'blob'
      }).then(function (res) {
        var blob = new Blob([res.data], {
          type: "image/jpeg"
        });
        var url = window.URL.createObjectURL(blob);
        _this.shareImg = url;
      });
    },
    cancelForm: function cancelForm() {
      this.$emit('changeVisible', false);
    },
    submitForm: function submitForm() {
      this.$emit('changeVisible', false);
    }
  }
};