Vue.directive('web-title', {
  inserted: function inserted(el, binding) {
    var value = binding.value;

    if (el.dataset.title) {
      // 方式1，可以给标签的data-title的属性赋值
      document.title = el.dataset.title;
    } else if (value && value.title) {
      // 方式2，指令传参
      document.title = value.title;
    }
  },
  update: function update(el, binding) {
    var value = binding.value;

    if (el.dataset.title) {
      document.title = el.dataset.title;
    } else if (value && value.title) {
      document.title = value.title;
    }
  }
});

Array.prototype.indexOf = function (val) {
  for (var i = 0; i < this.length; i++) {
    if (this[i] == val) return i;
  }

  return -1;
};

Array.prototype.remove = function (val) {
  var index = this.indexOf(val);

  if (index > -1) {
    this.splice(index, 1);
  }
};

export var GetTime = function GetTime(time) {
  var sp = arguments.length > 1 && arguments[1] !== undefined ? arguments[1] : '.';
  console.log(time, sp);
  if (!time) return '—';
  if (sp === '.') time = time.split('T')[0] + ' ' + time.split('T')[1];

  if (time.indexOf(sp) > -1) {
    return time.split(sp)[0];
  } else return time;
};
export var splitDate = function splitDate() {
  var todo = arguments.length > 0 && arguments[0] !== undefined ? arguments[0] : [];
  var time = arguments.length > 1 ? arguments[1] : undefined;
  //将数组todo按字段time降序分组
  if (!todo) return [];
  var dates = [];
  var newTodo = [];
  var j = 0;
  todo.forEach(function (item) {
    var t = GetTime(item[time], " ");

    if (dates.length === 0 || dates.indexOf(t) === -1) {
      dates.push(t);
      newTodo.push({
        date: dates[j++],
        subList: []
      });
    }
  });
  todo.forEach(function (item) {
    var t = GetTime(item[time], " ");

    if (dates.indexOf(t) > -1) {
      newTodo[dates.indexOf(t)].subList.push(item);
    }
  });
  newTodo.sort(function (a, b) {
    return Date.parse(b.date.replace(/-/g, "/")) - Date.parse(a.date.replace(/-/g, "/"));
  });
  return newTodo;
};

Vue.prototype.$addStorageEvent = function (key, data) {
  // 创建一个StorageEvent事件
  var newStorageEvent = document.createEvent('StorageEvent');
  var storage = {
    setItem: function setItem(k, val) {
      localStorage.setItem(k, val); // 初始化创建的事件

      newStorageEvent.initStorageEvent('setItem', false, false, k, null, val, null, null); // 派发对象

      window.dispatchEvent(newStorageEvent);
    }
  };
  return storage.setItem(key, data);
};

