import { deleteDoc, deleteForever, doCollect, fetchCollections, fetchCoworkers, fetchDocInfo, fetchDustbin, fetchMyDocs, fetchRecentDocs, fetchTeamDocs, removeCoworker, undoCollect, undoDelete } from "../api/api";
import { GetTime, splitDate } from "../main";
export default {
  name: "docImg",
  props: {
    type: {
      default: 'history' //'collection', 'build', 'dustbin', 'team'?

    },
    team: {
      default: ''
    }
  },
  data: function data() {
    return {
      isCo: false,
      role: -1,
      userId: localStorage.userId,
      tableData: [// {
        //     docId: 1,
        //     docName: '文档1',
        //     readTime: '2020-08-14 00:04:23.408300',
        //     isHover: false
        //   },
        //   {
        //     docId: 2,
        //     docName: '文档2',
        //     readTime: '2020-08-15 00:04:23.408300',
        //     isHover: false
        //   },
        //   {
        //     docId: 3,
        //     docName: '文档3',
        //     readTime: '2020-08-14 00:04:23.408300',
        //     isHover: false
        //   },
        //   {
        //     docId: 4,
        //     docName: '文档4',
        //     readTime: '2020-08-11 00:04:23.408300',
        //     isHover: false
        //   },
      ],
      folderDialog: false,
      docId: '1',
      shareId: '',
      shareV: false,
      setV: false,
      setId: '2',
      visi: 3,
      NAME: '',
      isTeam: false,
      todo: {},
      Dialog: false,
      Info: {}
    };
  },
  mounted: function mounted() {
    this.init();
  },
  methods: {
    init: function init() {
      var _this = this;

      console.log('team', this.team, this.type);

      if (this.type === 'history') {
        fetchRecentDocs().then(function (res) {
          if (res.status === 200) {
            _this.tableData = [];
            res.data.forEach(function (i) {
              _this.tableData.push({
                docId: i.document.id,
                docName: i.document.name,
                readTime: GetTime(i.read_time),
                isCollected: i.document.has_collect,
                builder: i.document.create_user.username,
                builderId: i.document.create_user.id,
                isHover: false
              });
            });
            _this.todo = splitDate(_this.tableData, "readTime");
          }
        }).catch(function (e) {
          _this.$message({
            message: e,
            type: 'error'
          });
        });
      } else if (this.type === 'build') {
        fetchMyDocs().then(function (res) {
          if (res.status === 200) {
            _this.tableData = [];
            res.data.forEach(function (i) {
              _this.tableData.push({
                docId: i.id,
                docName: i.name,
                builder: i.create_user.username,
                builderId: i.create_user.id,
                createTime: GetTime(i.create_time),
                lastTime: GetTime(i.modify_time),
                lastUser: i.last_modify_user ? i.last_modify_user.username : "—",
                lastUserId: i.last_modify_user ? i.last_modify_user.id : "",
                isCollected: i.has_collect,
                isHover: false
              });
            });
          }
        }).catch(function (e) {
          _this.$message({
            message: e,
            type: 'error'
          });
        });
      } else if (this.type === 'team') {
        console.log('params', this.$route.params.teamId);
        fetchTeamDocs(this.$route.params.teamId).then(function (res) {
          if (res.status === 200) {
            _this.tableData = [];
            res.data.forEach(function (i) {
              _this.tableData.push({
                docId: i.id,
                docName: i.name,
                builder: i.create_user_username,
                builderId: i.create_user,
                createTime: GetTime(i.create_time),
                lastTime: GetTime(i.modify_time),
                lastUser: i.last_modify_user_username,
                lastUserId: i.last_modify_user,
                isHover: false
              });
            });
          }
        }).catch(function (e) {
          if (e.response && e.response.status === 401) {
            _this.$message({
              message: '不是团队',
              type: 'error'
            });
          }
        });
      } else if (this.type === 'dustbin') {
        fetchDustbin().then(function (res) {
          if (res.status === 200) {
            _this.tableData = [];
            res.data.forEach(function (i) {
              _this.tableData.push({
                docId: i.document.id,
                docName: i.document.name,
                deleteTime: GetTime(i.delete_time),
                builder: i.document.create_user.username,
                builderId: i.document.create_user.id,
                isHover: false
              });
            });
          }
        }).catch(function (e) {
          return _this.$message({
            message: e.response.data,
            type: 'error'
          });
        });
      } else if (this.type === 'collection') {
        fetchCollections().then(function (res) {
          if (res.status === 200) {
            _this.tableData = [];
            res.data.forEach(function (i) {
              _this.tableData.push({
                docId: i.id,
                docName: i.name,
                isCollected: i.has_collect,
                builder: i.create_user.username,
                builderId: i.create_user.id,
                lastUser: i.last_modify_user ? i.last_modify_user.username : "—",
                lastUserId: i.last_modify_user ? i.last_modify_user.id : "",
                isHover: false
              });
            });
          }
        }).catch(function (e) {
          return _this.$message({
            message: e.response.data,
            type: 'error'
          });
        });
      }
    },
    toDoc: function toDoc(id) {
      this.$router.push({
        name: 'editorPage',
        params: {
          docId: id
        }
      });
    },
    toUser: function toUser(id) {
      this.$router.push({
        name: 'PersonInfo',
        params: {
          personId: id
        }
      });
    },
    handleClick: function handleClick(id) {
      this.getDocInfo(id);
      this.Dialog = true;
    },
    Delete: function Delete(id) {
      var _this2 = this;

      var message = this.type === 'dustbin' ? '确定要彻底删除它吗？' : '确定要删除它吗？';
      this.$confirm(message).then(function () {
        if (_this2.type === 'dustbin') {
          deleteForever(id).then(function (res) {
            if (res.status === 204) {
              _this2.init();

              _this2.$message({
                message: '已彻底删除文档!',
                type: 'warning'
              });
            }
          }).catch(function (e) {
            return _this2.$message({
              message: e.response.data,
              type: 'error'
            });
          });
        } else {
          //删除文档
          deleteDoc(id).then(function (res) {
            if (res.status === 204) {
              _this2.init();

              _this2.$message({
                message: '删除成功！',
                type: 'info'
              });
            }
          }).catch(function (e) {
            if (e.response.status === 401) _this2.$message({
              message: "您没有权限！",
              type: 'error'
            });
          });
        }

        _this2.Dialog = false;
      });
    },
    shareDialog: function shareDialog(id) {
      this.shareId = id;
      this.NAME = this.Info.docName;
      this.shareV = true;
    },
    changeShare: function changeShare(val) {
      this.shareV = val;
    },
    changeVisible: function changeVisible(val, id) {
      this.folderDialog = val;
      this.getDocInfo(id); //或许没有用
    },
    openFolderDialog: function openFolderDialog(id) {
      this.docId = id;
      var that = '';
      this.tableData.forEach(function (i) {
        if (i.docId + '' === id + '') {
          that = i.docName;
          return;
        }
      });
      this.NAME = that;
      this.folderDialog = true;
    },
    changeColl: function changeColl(id) {
      var _this3 = this;

      var item = {};
      this.tableData.forEach(function (i) {
        if (i.docId + '' === id + '') {
          item = i;
        }
      });

      if (item.isCollected) {
        undoCollect(item.docId).then(function (res) {
          if (res.status === 204) {
            _this3.init();

            _this3.$message({
              message: '取消收藏成功',
              type: 'info'
            });

            item.isCollected = false;
            return;
          }
        }).catch(function (e) {
          return _this3.$message({
            message: e.response.data,
            type: 'error'
          });
        });
      } else {
        doCollect(item.docId).then(function (res) {
          if (res.status === 201) {
            _this3.init();

            item.isCollected = true;

            _this3.$message({
              message: '收藏成功',
              type: 'info'
            });

            return;
          }
        }).catch(function (e) {
          if (e.response.status === 400) {
            _this3.$message({
              message: '本文已被收藏',
              type: 'error'
            });
          }
        });
      }
    },
    recover: function recover(id) {
      var _this4 = this;

      undoDelete(id).then(function (res) {
        if (res.status === 200) {
          _this4.init();

          _this4.$message({
            message: '恢复文档成功！',
            type: 'info'
          });

          _this4.init(); //再获取一遍回收站

        }
      }).catch(function (e) {
        return _this4.$message({
          message: e.response.data,
          type: 'error'
        });
      });
    },
    setP: function setP(id) {
      this.setId = id;
      this.visi = this.Info.role;
      this.isTeam = this.Info.isTeam;
      this.NAME = this.Info.docName;
      this.setV = true;
    },
    changeP: function changeP(val, id) {
      this.setV = val;
      this.getDocInfo(id);
    },
    checkMove: function checkMove(item) {
      var _this5 = this;

      var message = '确定要移除ta吗？';
      this.$confirm(message).then(function () {
        removeCoworker(_this5.Info.id, item.userId).then(function (res) {
          if (res.status === 200) {
            _this5.Info.coworkers = [];
            res.data.forEach(function (i, index) {
              if (i.id + '' === _this5.userId) {
                _this5.isCo = true;
                _this5.role = i.role;
              }

              if (index === 0) {
                _this5.Info.builder = i.username;
                _this5.Info.builderId = i.id;
              }

              _this5.Info.coworkers.push({
                userId: i.id,
                userName: i.username,
                userImg: i.head,
                isBuilder: i.role === 1 ? true : false
              });
            });

            _this5.init();

            _this5.Dialog = false;

            _this5.$message({
              message: '移除成功',
              type: 'info'
            });
          } else {
            _this5.$message({
              message: '发生其他错误',
              type: 'error'
            });
          }
        }).catch(function (e) {
          if (e.response.status === 401) {
            _this5.$message({
              message: '您没有删除权限或此人不在协作者中',
              type: 'error'
            });
          }
        });
      });
    },
    getDocInfo: function getDocInfo(id) {
      var _this6 = this;

      fetchDocInfo(id).then(function (res) {
        if (res.status === 200) {
          _this6.Info.id = res.data.id;
          _this6.Info.docName = res.data.name;
          _this6.Info.role = res.data.role;
          _this6.Info.isDoc = !res.data.type;
          _this6.Info.isTeam = _this6.Info.isDoc && res.data.parent_doc;
          _this6.Info.modify_time = GetTime(res.data.modify_time);
          _this6.Info.create_time = GetTime(res.data.create_time);
          fetchCoworkers(_this6.Info.id).then(function (res) {
            if (res.status === 200) {
              _this6.isCo = false;
              _this6.Info.coworkers = [];
              res.data.forEach(function (i, index) {
                if (i.id + '' === _this6.userId) {
                  _this6.isCo = true;
                  _this6.role = i.role;
                }

                if (index === 0) {
                  _this6.Info.builder = i.username;
                  _this6.Info.builderId = i.id;
                }

                _this6.Info.coworkers.push({
                  userId: i.id,
                  userName: i.username,
                  userImg: i.head,
                  isBuilder: i.role === 1 ? true : false
                });
              });
            }
          }).catch(function (e) {
            return _this6.$message({
              message: e.response.data,
              type: 'error'
            });
          });
        } else if (res.status === 204) {
          _this6.$message({
            message: '其他错误',
            type: 'error'
          });
        }
      }).catch(function (e) {
        _this6.$message({
          message: e.response.data,
          type: 'error'
        });
      });
    }
  }
};