import { addCoworker, createDoc, fetchUsers } from "../api/api";
export default {
  name: "folderDialog",
  props: {
    dialog: {
      default: false
    },
    docId: {
      default: ''
    },
    type: {
      default: '' //coworker, teamworker, newteam

    },
    name: {
      default: ''
    }
  },
  data: function data() {
    return {
      form: {
        name: '',
        id: null
      },
      isV: false,
      state: '',
      newCo: []
    };
  },
  watch: {
    dialog: function dialog(val) {
      this.isV = val;
      this.form.name = '';
    }
  },
  methods: {
    cancelForm: function cancelForm() {
      this.$emit('changeVisible', false, this.docId);
    },
    submitForm: function submitForm() {
      var _this = this;

      if (this.type !== 'newteam') {
        addCoworker(this.docId, this.form.id).then(function (res) {
          if (res.status === 200) {
            _this.$message({
              message: '添加成功',
              type: 'info'
            });
          } else if (res.status === 204) {
            _this.$message({
              message: '用户不存在',
              type: 'error'
            });
          }
        }).catch(function (e) {
          if (e.response.status === 401) {
            _this.$message({
              message: '您没有权限',
              type: 'error'
            });
          } else if (e.response.status === 400) {
            _this.$message({
              message: '此用户已在协作者中',
              type: 'error'
            });
          }
        });
        this.$emit('changeVisible', false, this.docId);
      } else {
        createDoc(1, this.form.name).then(function (res) {
          if (res.status === 201) {
            _this.$message({
              message: '创建团队成功！',
              type: 'info'
            });

            _this.$emit('changeVisible', false, _this.docId);
          }
        }).catch(function (e) {
          if (e.response.status === 401) {
            _this.$message({
              message: '您没有权限',
              type: 'error'
            });
          }
        });
      }
    },
    querySearchAsync: function querySearchAsync(queryString, cb) {
      var _this2 = this;

      //let users = this.users;//按照queryString去后端请求，将返回的数据sb(results)
      var results = [];
      fetchUsers(queryString).then(function (res) {
        if (res.status === 200) {
          if (res.data.length === 0) {//this.$message({message:'暂无匹配', type:'error'})
          } else {
            console.log(res.data);
            res.data.forEach(function (i) {
              results.push({
                id: i.id,
                value: i.username
              });
            }); //"id": 1, "username": "aaa", "head": "", "email": "", "mobile": "17600294315"
          }
        }
      }).catch(function (e) {
        _this2.$message({
          message: e.response.data,
          type: 'error'
        });
      }); //若无匹配，$message提示暂无匹配

      console.log(results);
      cb(results); // let results = queryString ? users.filter(this.createStateFilter(queryString)) : null;
      // cb(results);
    },
    handleSelect: function handleSelect(item) {
      this.form.name = item.value;
      this.form.id = item.id;
    },
    createStateFilter: function createStateFilter(queryString) {
      //给filter()的参数函数包了一层外壳。
      return function (s) {
        //filter()函数的参数，返回为true时元素保留。该函数的第一个参数是当前元素值即s。
        console.log(s);
        return s.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
      };
    }
  },
  mounted: function mounted() {}
};